/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import paymentsData from './data-bb-payments-http';

const paymentsDataModuleKey = 'data-bb-payments-http-ng';

export const paymentsDataKey = 'data-bb-payments-http-ng:paymentsData';

export default ng
  .module(paymentsDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(paymentsDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       paymentsData(config),
      ],
    };
  }])
  .name;



